export default {
  name: 'not_found',
  data: function data() {
    return {
      count: 5,
      timer: null
    };
  },
  created: function created() {
    var _this = this;

    this.timer = setInterval(function () {
      if (_this.count > 1) {
        _this.count--;
      } else {
        _this.$router.replace('/');
      }
    }, 1000);
  },
  destroyed: function destroyed() {
    clearInterval(this.timer);
  }
};